.title-sm-mobile {
    font-size: 6em;
    text-align: center;
}

.title-tablet {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.5em;
    font-size: 4em;
}

.title-sm-screen-laptop {
    display: flex;
    font-size: 6.5em;
    flex-direction: column;
    width: 650px;
    margin: 1em auto;
}

.title-sm-screen-laptop-main-left{
    align-self: flex-start;
}

.title-sm-screen-laptop-main-right{
    align-self: flex-end;
}

.title-desktop {
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-size: 9em;
    margin: 0.5em auto;
    width: 900px;
}
