.landing-card-container {
	z-index: 2;
	height: 100vh;
	width: 100vw;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}

.landing-card-arrow-down {
	-webkit-animation: bounce-top 1.7s both;
	animation: bounce-top 1.7s both;
}

.landing-card-hero-image {
	margin-bottom: 10em;
}

.landing-card-hero-image > p {
	text-align: center;
	margin: 0;
	font-size: 2em;
}

